import React from "react";
import Row from "muicss/lib/react/row";
import Col from "muicss/lib/react/col";
import Select from "muicss/lib/react/select";
import Option from "muicss/lib/react/option";
import PercentageInput from "../common/PercentageInput";

class QuoteWaterfall extends React.Component {
  render() {
    let waterfallType = "";
    waterfallType = this.props.waterfallTypesList.find(
      (t) => t.list_type_id === this.props.tier.waterfall_type
    );
    waterfallType = waterfallType ? waterfallType.list_type_id : "";
    return (
      <div>
        <Row>
          <Row>
            <p className="bold-text panel-info-title">
              Tier {this.props.index + 1}
            </p>
          </Row>
          <Row>
            <Col md="12">
              <Col md="3" className="quote-col-left">
                <Select
                  value={waterfallType}
                  onChange={this.props.waterfallTypeChange.bind(
                    this,
                    this.props.index
                  )}
                  label="Waterfall Type"
                >
                  {this.props.waterfallTypesList.map((i, index) => {
                    return (
                      <Option
                        key={index}
                        value={i.list_type_id}
                        label={i.value}
                      />
                    );
                  })}
                </Select>
              </Col>
              <Col md="3" className="quote-col-left">
                <Select
                  value={this.props.tier.priority}
                  onChange={this.props.priorityChange.bind(
                    this,
                    this.props.index
                  )}
                  label="Priority to Investor"
                >
                  <Option value={1} label="Yes" />
                  <Option value={0} label="No" />
                </Select>
              </Col>
              <Col md="3" className="quote-col-left">
                <PercentageInput
                  label="Hurdle %"
                  value={this.props.tier.hurdle_multiple}
                  decimalPlaces={3}
                  required={true}
                  update={this.props.hurdleChange.bind(this, this.props.index)}
                />
              </Col>
              <Col md="3" className="quote-col-left">
                <PercentageInput
                  label="Promote %"
                  value={this.props.tier.promote}
                  decimalPlaces={3}
                  required={true}
                  update={this.props.promoteChange.bind(this, this.props.index)}
                />
              </Col>
            </Col>
          </Row>
        </Row>
      </div>
    );
  }
}
export default QuoteWaterfall;
