import React from "react";
import QuoteForm from "./QuoteForm";
import { Navigation } from "../../lib";
import Row from "muicss/lib/react/row";

class QuoteFormPage extends React.Component {
  render() {
    return (
      <div>
        <Row className="sub-header">
          <div className="sub-header-content flex-justify-center">
            <div className="flex-center">
              <div
                className="back-arrow pointer"
                onClick={() =>
                  Navigation.go(
                    `/fund-portfolio/${this.props.type}${
                      this.props.page ? `?page=${this.props.page}` : ""
                    }`
                  )
                }
              >
                <img src="/images/icons/arrow-left.png" alt="go back" />
              </div>
            </div>
          </div>
        </Row>
        <div>
          <div className="quote">
            <QuoteForm
              quoteId={this.props.quoteId}
              type={this.props.type}
              equity_type={this.props.equity_type}
              waterfallTypesList={this.props.waterfallTypesList}
              quote={this.props.quote}
              newNoteId={this.props.newNoteId}
              noteSuccess={this.props.noteSuccess}
              createNewQuoteNote={this.props.createNewQuoteNote}
              editQuoteNote={this.props.editQuoteNote}
              deleteQuoteNote={this.props.deleteQuoteNote}
              saveEquityQuote={this.props.saveEquityQuote}
            />
          </div>
        </div>
      </div>
    );
  }
}

export default QuoteFormPage;
