import React from "react";
import Panel from "muicss/lib/react/panel";
import Row from "muicss/lib/react/row";
import Col from "muicss/lib/react/col";
import Form from "muicss/lib/react/form";
import Input from "muicss/lib/react/input";
import Button from "muicss/lib/react/button";
import CurrencyInput from "../common/CurrencyInput";
import QuoteNotes from "./QuoteFormNotes";
import { formatDate } from "../../lib";
import PercentageInput from "../common/PercentageInput";
import QuoteWaterfall from "./QuoteWaterfall";
import { Navigation } from "../../lib";

class QuoteForm extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      quoted_amount: null,
      minimum_multiple: null,
      term: null,
      origination_fee: null,
      exit_fee: null,
      am_fee: null,
      ltv: null,
      ltc: null,
      pref_rate: null,
      minimum_current_pay: null,
      percent_of_equity: null,
      preferred_irr: null,
      sponsor_acq_fee: null,
      sponsor_pm_fee: null,
      sponsor_am_fee: null,
      sponsor_cm_fee: null,
      notes: [],
      tiers: [],
    };
  }

  UNSAFE_componentWillMount() {
    let quote = this.props.quote;

    this.setState({
      quoted_amount: quote.quoted_amount,
      minimum_multiple: quote.minimum_expected_return,
      term: quote.term,
      origination_fee: quote.origination_fee,
      exit_fee: quote.exit_fee,
      am_fee: quote.investor_asset_mgmt_fee,
      ltv: quote.ltv,
      ltc: quote.ltc,
      pref_rate: quote.pref_rate,
      minimum_current_pay: quote.minimum_payout,
      percent_of_equity: quote.percent_of_ownership,
      preferred_irr: quote.preferred_irr,
      sponser_acq_fee: quote.sponsor_acquisition_fee,
      sponser_pm_fee: quote.sponsor_property_mgmt_fee,
      sponser_am_fee: quote.sponsor_asset_mgmt_fee,
      sponser_cm_fee: quote.sponsor_construction_mgmt_fee,
      tiers: quote.tiers
        ? quote.tiers.map((tier) => ({
            sort_order: tier.sort_order,
            waterfall_type: tier.waterfall_type,
            priority: tier.priority ? 1 : 0,
            promote: tier.promote,
            hurdle_multiple: tier.hurdle_multiple,
          }))
        : "",
      sponsor_acq_fee: quote.sponsor_acquisition_fee,
      sponsor_pm_fee: quote.sponsor_property_mgmt_fee,
      sponsor_am_fee: quote.sponsor_asset_mgmt_fee,
      sponsor_cm_fee: quote.sponsor_construction_mgmt_fee,
      notes: quote.notes ? quote.notes : [],
    });
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (nextProps.quote.notes !== this.props.quote.notes) {
      this.setState({ notes: nextProps.quote.notes });
    }
  }

  quotedAmountChange(e) {
    this.setState({ quoted_amount: e });
  }

  minMultipleChange(e) {
    let value = e.target.value;
    if (!value.match(/^\d+(\.\d{0,2})?$/g)) {
      value = value.slice(0, -1);
    }
    this.setState({ minimum_multiple: value });
  }

  termChange(e) {
    this.setState({ term: e.target.value.replace(/[^0-9]/g, "") });
  }

  percentageInputChange(e, name) {
    this.setState({ [name]: e ? parseFloat(e) / 100 : null });
  }

  submitNote(note, noteId) {
    noteId
      ? this.props.editQuoteNote(noteId, note)
      : this.props.createNewQuoteNote(this.props.quoteId, note);
  }

  async deleteNote(noteId) {
    await this.props.deleteQuoteNote(noteId);
  }

  addTierToLocalArray(e) {
    e.preventDefault();
    let lastItemInArray = this.state.tiers.length - 1;
    let newWaterfallTierObject = {
      sort_order: lastItemInArray
        ? this.state.tiers[lastItemInArray].sort_order + 1
        : "",
      waterfall_type: lastItemInArray
        ? this.state.tiers[lastItemInArray].waterfall_type
        : "",
      priority: lastItemInArray
        ? this.state.tiers[lastItemInArray].priority
        : "",
      promote: "",
      hurdle_multiple: "",
    };
    let tiers = this.state.tiers;
    tiers.push(newWaterfallTierObject);
    this.setState({ tiers: tiers });
  }

  sharedSelectChangeHandler(name, index, e) {
    let tiers = this.state.tiers;
    tiers[index][name] = parseInt(e.target.value, 10);
    this.setState({
      tiers,
    });
  }

  sharedInputChangeHandler(name, index, e) {
    let tiers = this.state.tiers;
    tiers[index][name] = parseFloat(e) / 100;
    this.setState({
      tiers,
    });
  }

  saveQuote(e) {
    e.preventDefault();
    this.props.saveEquityQuote(
      parseInt(this.props.quoteId, 10),
      this.state.quoted_amount,
      this.state.term,
      this.state.ltv,
      this.state.ltc,
      this.state.minimum_current_pay,
      this.state.pref_rate,
      this.state.origination_fee,
      this.state.sponsor_acq_fee,
      this.state.sponsor_pm_fee,
      this.state.sponsor_am_fee,
      this.state.am_fee,
      this.state.sponsor_cm_fee,
      this.state.exit_fee,
      this.state.preferred_irr,
      this.state.minimum_multiple,
      this.state.tiers
    );
    Navigation.go(
      `/fund-portfolio/${this.props.type}${window.location.search}`
    );
  }

  render() {
    return (
      <Panel className="quote-panel fp-quote-panel">
        <div>
          <div>
            <Row>
              <p className="quote-bank-name">Quote</p>
            </Row>
            <Form>
              <Row>
                <Col md="3" sm="8" className="quote-col-left">
                  <Input
                    label="Quoted Date"
                    value={formatDate(this.props.quote.quoted_at)}
                    disabled={true}
                    onChange={() => {}}
                  />
                </Col>
                <Col md="3" sm="8" className="quote-col-left">
                  <CurrencyInput
                    label="Quoted Amount"
                    value={this.state.quoted_amount}
                    decimalPlaces={0}
                    update={this.quotedAmountChange.bind(this)}
                  />
                </Col>
                <Col md="3" sm="8" className="quote-col-left">
                  <Input
                    label="Minimum Multiple"
                    value={this.state.minimum_multiple || ""}
                    onBlur={() => {}}
                    onChange={this.minMultipleChange.bind(this)}
                  />
                </Col>
                <Col md="3" sm="8" className="quote-col-left">
                  <Input
                    label="Term (# in years)"
                    value={this.state.term || ""}
                    onChange={this.termChange.bind(this)}
                  />
                </Col>
              </Row>

              {this.props.equity_type === "JV" ? (
                <Row>
                  <Col md="3" sm="8" className="quote-col-left">
                    <PercentageInput
                      label="Percent of Equity %"
                      value={this.state.percent_of_equity}
                      decimalPlaces={3}
                      update={(e) =>
                        this.percentageInputChange(e, "percent_of_equity")
                      }
                    />
                  </Col>
                  <Col md="3" sm="8" className="quote-col-left">
                    <PercentageInput
                      label="Preferred Return %"
                      value={this.state.preferred_irr}
                      decimalPlaces={3}
                      update={(e) =>
                        this.percentageInputChange(e, "preferred_irr")
                      }
                    />
                  </Col>
                </Row>
              ) : (
                <Row>
                  <Col md="3" sm="8" className="quote-col-left">
                    <PercentageInput
                      label="LTV %"
                      value={this.state.ltv}
                      decimalPlaces={3}
                      update={(e) => this.percentageInputChange(e, "ltv")}
                    />
                  </Col>
                  <Col md="3" sm="8" className="quote-col-left">
                    <PercentageInput
                      label="LTC %"
                      value={this.state.ltc}
                      decimalPlaces={3}
                      update={(e) => this.percentageInputChange(e, "ltc")}
                    />
                  </Col>
                  <Col md="3" sm="8" className="quote-col-left">
                    <PercentageInput
                      label="Pref Rate %"
                      value={this.state.pref_rate}
                      decimalPlaces={3}
                      update={(e) => this.percentageInputChange(e, "pref_rate")}
                    />
                  </Col>
                  <Col md="3" sm="8" className="quote-col-left">
                    <PercentageInput
                      label="Min Current Pay %"
                      value={this.state.minimum_current_pay}
                      decimalPlaces={3}
                      update={(e) =>
                        this.percentageInputChange(e, "minimum_current_pay")
                      }
                    />
                  </Col>
                </Row>
              )}

              <div>
                <Row>
                  <p className="bold-text panel-info-title">Investor Fees</p>
                </Row>
                <Row>
                  <Col md="3" sm="8" className="quote-col-left">
                    <PercentageInput
                      label="Origination Fee %"
                      value={this.state.origination_fee}
                      decimalPlaces={3}
                      update={(e) =>
                        this.percentageInputChange(e, "origination_fee")
                      }
                    />
                  </Col>
                  <Col md="3" sm="8" className="quote-col-left">
                    <PercentageInput
                      label="Exit Fee %"
                      value={this.state.exit_fee}
                      decimalPlaces={3}
                      update={(e) => this.percentageInputChange(e, "exit_fee")}
                    />
                  </Col>
                  <Col md="3" sm="8" className="quote-col-left">
                    <PercentageInput
                      label="AM Fee %"
                      value={this.state.am_fee}
                      decimalPlaces={3}
                      update={(e) => this.percentageInputChange(e, "am_fee")}
                    />
                  </Col>
                </Row>
              </div>

              <div>
                <Row>
                  <p className="bold-text panel-info-title">Sponsor Fees</p>
                </Row>
                <Row>
                  <Col md="3" sm="8" className="quote-col-left">
                    <PercentageInput
                      label="ACQ Fee %"
                      value={this.state.sponsor_acq_fee}
                      decimalPlaces={3}
                      update={(e) =>
                        this.percentageInputChange(e, "sponsor_acq_fee")
                      }
                    />
                  </Col>
                  <Col md="3" sm="8" className="quote-col-left">
                    <PercentageInput
                      label="PM Fee %"
                      value={this.state.sponsor_pm_fee}
                      decimalPlaces={3}
                      update={(e) =>
                        this.percentageInputChange(e, "sponsor_pm_fee")
                      }
                    />
                  </Col>
                  <Col md="3" sm="8" className="quote-col-left">
                    <PercentageInput
                      label="AM Fee %"
                      value={this.state.sponsor_am_fee}
                      decimalPlaces={3}
                      update={(e) =>
                        this.percentageInputChange(e, "sponsor_am_fee")
                      }
                    />
                  </Col>
                  <Col md="3" sm="8" className="quote-col-left">
                    <PercentageInput
                      label="CM Fee %"
                      value={this.state.sponsor_cm_fee}
                      decimalPlaces={3}
                      update={(e) =>
                        this.percentageInputChange(e, "sponsor_cm_fee")
                      }
                    />
                  </Col>
                </Row>
              </div>
              {this.props.equity_type === "JV" ? (
                <div>
                  {this.state.tiers &&
                    this.state.tiers.map((tier, i) => {
                      return (
                        <QuoteWaterfall
                          key={i}
                          tier={tier}
                          index={i}
                          waterfallTypesList={this.props.waterfallTypesList}
                          waterfallTypeChange={this.sharedSelectChangeHandler.bind(
                            this,
                            "waterfall_type"
                          )}
                          priorityChange={this.sharedSelectChangeHandler.bind(
                            this,
                            "priority"
                          )}
                          hurdleChange={this.sharedInputChangeHandler.bind(
                            this,
                            "hurdle_multiple"
                          )}
                          promoteChange={this.sharedInputChangeHandler.bind(
                            this,
                            "promote"
                          )}
                        />
                      );
                    })}
                  <div className="quote-add-note">
                    <p
                      className="orange-text"
                      onClick={this.addTierToLocalArray.bind(this)}
                    >
                      <i className="material-icons icon-left">add</i>Add
                      Waterfall Tier
                    </p>
                  </div>
                </div>
              ) : (
                ""
              )}
            </Form>
          </div>
          <QuoteNotes
            quote_id={this.props.quoteId}
            notes={this.state.notes}
            deleteNote={this.deleteNote.bind(this)}
            submitNote={this.submitNote.bind(this)}
          />
        </div>
        <div className="fp-quote-panel-footer">
          <Button
            variant="flat"
            className="save-quote-btn mui--z1"
            onClick={this.saveQuote.bind(this)}
          >
            Save
          </Button>
        </div>
      </Panel>
    );
  }
}

export default QuoteForm;
