import React from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import * as fundPortfolioActions from "../../actions/fundPortfolioActions";
import * as dealDetailsActions from "../../actions/dealDetailsActions";
import QuoteFormPage from "./QuoteFormPage";
import getParameterByName from "../common/getParameterByName";
import Modal from "react-modal";

class QuoteFormContainer extends React.Component {
  async UNSAFE_componentWillMount() {
    await this.props.actions.getEquityQuoteById(
      this.props.match.params.quoteId
    );
    this.props.dealDetailsActions.getWaterfallTypesList();
  }

  render() {
    return (
      <div>
        {!this.props.fundPortfolios.loading &&
        this.props.fundPortfolios.quote ? (
          <QuoteFormPage
            quoteId={this.props.match.params.quoteId}
            type={this.props.match.params.type}
            page={getParameterByName("page")}
            quote={this.props.fundPortfolios.quote}
            waterfallTypesList={this.props.dealDetails.waterfallTypesList}
            equity_type={
              getParameterByName("equity_type") === "null"
                ? "JV"
                : getParameterByName("equity_type")
            }
            newNoteId={this.props.fundPortfolios.new_note_id}
            noteSuccess={this.props.fundPortfolios.note_success}
            createNewQuoteNote={this.props.actions.createNewQuoteNote}
            editQuoteNote={this.props.actions.editQuoteNote}
            deleteQuoteNote={this.props.actions.deleteQuoteNote}
            saveEquityQuote={this.props.actions.saveEquityQuote}
          />
        ) : (
          <Modal
            className="loading-modal"
            ariaHideApp={false}
            shouldCloseOnOverlayClick={false}
            contentLabel="modal"
            isOpen={true}
          >
            <div className="centered-spinner">
              <img
                src="/images/icons/spin.svg"
                className="loading-spinner"
                alt="loading spinner"
              />
            </div>
          </Modal>
        )}
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    fundPortfolios: state.fundPortfolios,
    dealDetails: state.dealDetails,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(fundPortfolioActions, dispatch),
    dealDetailsActions: bindActionCreators(dealDetailsActions, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(QuoteFormContainer);
