import * as types from "./actionTypes";
import config from "../config";
import { ajax } from "../lib";
import { handleAPIError } from "../lib";

export function getFundPortfolioList(params) {
  return async (dispatch) => {
    dispatch({ type: types.LOADING_FUND_PORTFOLIOS });
    ajax({
      url: `${config.BASE_URL}funds/quotes`,
      applyAuthHeader: true,
      data: { ...params },
      success: function (response) {
        dispatch({
          type: types.GET_FUND_PORTFOLIO_SUCCESS,
          data: response.data,
          nextPageUrl: response.next_page_url,
        });
      },
      error: handleAPIError(dispatch, types.GET_FUND_PORTFOLIO_FAILED),
    });
  };
}

export function getFullContactInfo(contact_id) {
  return (dispatch) => {
    dispatch({ type: types.LOADING_FULL_CONTACT });
    ajax({
      url: `${config.BASE_URL}contact/${contact_id}`,
      applyAuthHeader: true,
      success: function (response) {
        if (response.data && response.data.length >= 1) {
          dispatch({
            type: types.LOAD_FULL_CONTACT,
            contact: response.data[0].contacts[0],
          });
        } else {
          dispatch({ type: types.NO_FULL_CONTACT });
        }
      },
    });
  };
}

export function getEquityQuoteById(quote_id) {
  return async (dispatch) => {
    dispatch({ type: types.LOADING_EQUITY_QUOTE });
    await ajax({
      url: `${config.BASE_URL}equity-quotes/${quote_id}`,
      applyAuthHeader: true,
      success: function (response) {
        dispatch({
          type: types.GET_EQUITY_QUOTE_SUCCESS,
          data: response.data || {},
        });
        dispatch(getEquityQuoteNotes(quote_id));
      },
      error: handleAPIError(dispatch, types.GET_EQUITY_QUOTE_FAILED),
    }).catch((err) => {});
  };
}

export function getEquityQuoteNotes(quote_id) {
  return (dispatch) => {
    ajax({
      url: `${config.BASE_URL}equity-quotes/${quote_id}/notes`,
      applyAuthHeader: true,
      success: function (response) {
        dispatch({
          type: types.GET_QUOTE_NOTE_SUCCESS,
          data: response.data.reverse() || [],
        });
      },
      error: handleAPIError(dispatch, types.GET_QUOTE_NOTE_FAILED),
    });
  };
}

export function saveEquityQuote(
  quote_id,
  quoted_amount,
  term,
  ltv,
  ltc,
  minimum_payout,
  pref_rate,
  origination_fee,
  sponsor_acquisition_fee,
  sponsor_property_mgmt_fee,
  sponsor_asset_mgmt_fee,
  investor_asset_mgmt_fee,
  sponsor_construction_mgmt_fee,
  exit_fee,
  preferred_irr,
  minimum_expected_payout,
  waterfall_tiers
) {
  return (dispatch) => {
    ajax({
      url: `${config.BASE_URL}equity-quotes/${quote_id}`,
      applyAuthHeader: true,
      data: {
        quoted_amount,
        term,
        ltv,
        ltc,
        minimum_payout,
        pref_rate,
        origination_fee,
        sponsor_acquisition_fee,
        sponsor_property_mgmt_fee,
        sponsor_asset_mgmt_fee,
        investor_asset_mgmt_fee,
        sponsor_construction_mgmt_fee,
        exit_fee,
        preferred_irr,
        minimum_expected_payout,
        waterfall_tiers,
      },
      type: "PUT",
      success: function (response) {
        dispatch({
          type: types.SAVE_EQUITY_QUOTE,
          success: true,
          message: response.message,
        });
      },
      error: handleAPIError(dispatch, types.SAVE_EQUITY_QUOTE),
    });
  };
}

export function createNewQuoteNote(quote_id, note) {
  return async (dispatch) => {
    await ajax({
      url: `${config.BASE_URL}equity-quotes/${quote_id}/notes`,
      applyAuthHeader: true,
      data: { note },
      type: "POST",
      success: function (response) {
        dispatch({
          type: types.CREATE_QUOTE_NOTE_SUCCESS,
          data: response.data,
          message: response.message,
        });
      },
      error: handleAPIError(dispatch, types.UPDATE_QUOTE_NOTE_FAILED),
    });
  };
}

export function editQuoteNote(note_id, note) {
  return (dispatch) => {
    ajax({
      url: `${config.BASE_URL}equity-quotes/notes/${note_id}`,
      applyAuthHeader: true,
      data: { note },
      type: "PUT",
      success: function (response) {
        dispatch({
          type: types.EDIT_QUOTE_NOTE_SUCCESS,
          data: response.data,
          message: response.message,
        });
      },
      error: handleAPIError(dispatch, types.UPDATE_QUOTE_NOTE_FAILED),
    });
  };
}

export function deleteQuoteNote(note_id) {
  return async (dispatch) => {
    await ajax({
      url: `${config.BASE_URL}equity-quotes/notes/${note_id}`,
      applyAuthHeader: true,
      type: "DELETE",
      success: function () {
        dispatch({
          type: types.DELETE_QUOTE_NOTE_SUCCESS,
          note_id,
          message: "Note Deleted Successfully",
        });
      },
      error: handleAPIError(dispatch, types.UPDATE_QUOTE_NOTE_FAILED),
    }).catch((err) => {});
  };
}

export function passEquityQuote(quote_id) {
  return (dispatch) => {
    ajax({
      url: `${config.BASE_URL}equity-quotes/${quote_id}`,
      applyAuthHeader: true,
      data: { status: 2 },
      type: "PUT",
      success: function (response) {
        dispatch({
          type: types.SAVE_EQUITY_QUOTE,
          success: true,
          message: response.message,
        });
      },
      error: handleAPIError(dispatch, types.SAVE_EQUITY_QUOTE),
    });
  };
}

export function closeToast() {
  return { type: types.CLOSE_FUND_PORTFOLIO_TOAST };
}
