import FormattedInput from "./FormattedInput";
import { formatWithCommas } from "../../lib";

class PercentageInput extends FormattedInput {
  constructor(props) {
    super(props);

    this.state = {
      formattedValue: "",
    };
  }

  UNSAFE_componentWillMount() {
    let formattedValue = this.props.value
      ? this.formatValue(
          +parseFloat(this.props.value * 100).toFixed(
            this.props.decimalPlaces || 2
          )
        )
      : "";
    this.setState({ formattedValue: formattedValue });
    this.validate(
      this.props.value ? this.props.value * 100 : null,
      this.props.minValue ? this.props.minValue * 100 : null,
      this.props.maxValue ? this.props.maxValue * 100 : null,
      this.props.required
    );
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (
      this.props.value !== nextProps.value ||
      this.props.minValue !== nextProps.minValue ||
      this.props.maxValue !== nextProps.maxValue
    ) {
      let formattedValue = nextProps.value
        ? this.formatValue(
            +parseFloat(nextProps.value * 100).toFixed(
              nextProps.decimalPlaces || 2
            )
          )
        : "";
      this.setState({ formattedValue: formattedValue });
      this.validate(
        nextProps.value ? nextProps.value * 100 : null,
        nextProps.minValue ? nextProps.minValue * 100 : null,
        nextProps.maxValue ? nextProps.maxValue * 100 : null,
        nextProps.required
      );
    }
  }

  formatValue(value) {
    value = value.toString();
    let regexExp =
      this.props.decimalPlaces === 0
        ? new RegExp("^\\d*?$")
        : new RegExp(
            "^\\d*(\\.\\d{0," + (this.props.decimalPlaces || 2) + "})?$"
          );
    if (!value.match(regexExp)) {
      value = value.slice(0, -1);
    }
    return value ? formatWithCommas(value) + "%" : "";
  }

  stripFormat(value) {
    return value ? value.replace(/[^0-9.]/g, "") : "";
  }
}

export default PercentageInput;
