import React from "react";
import moment from "moment";
import NoteModal from "../common/NoteModal";
import ConfirmationModal from "../common/ConfirmationModal";

class QuoteFormNotes extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      note_id: null,
      new_note: "",
      noteModalOpen: false,
      showAllNotes: false,
      showDeleteModal: false,
    };

    this.openNoteModal = this.openNoteModal.bind(this);
    this.closeNoteModal = this.closeNoteModal.bind(this);
    this.showAllNotes = this.showAllNotes.bind(this);
  }

  showAllNotes() {
    this.setState({ showAllNotes: !this.state.showAllNotes });
  }

  openNoteModal(e, note_id, note) {
    e.preventDefault();
    this.setState({ noteModalOpen: true, note_id, new_note: note });
  }

  closeNoteModal(e) {
    e.preventDefault();
    this.setState({ noteModalOpen: false, new_note: "" });
  }

  openConfirmDeleteModal(e, note_id) {
    this.setState({ showDeleteModal: true, note_id });
  }

  deleteNote(e) {
    e.preventDefault();
    this.props.deleteNote(this.state.note_id);

    this.setState({ showDeleteModal: false, note_id: null, new_note: "" });
  }

  submitNote(e, note) {
    e.preventDefault();
    this.props.submitNote(note, this.state.note_id);

    this.setState({ noteModalOpen: false, new_note: "", note_id: null });
  }

  render() {
    return (
      <div className="quote-notes-container">
        <p className="quote-notes-header">Notes</p>
        {this.props.notes && this.props.notes.length >= 1
          ? this.props.notes.map((note, index) => (
              <div
                key={index}
                className="note"
                hidden={!this.state.showAllNotes && index >= 5}
              >
                <p>
                  {note.note}&nbsp;
                  {note.is_editable && (
                    <span>
                      <img
                        src="/images/pencil.png"
                        id="pencil"
                        alt="pencil"
                        onClick={(e) =>
                          this.openNoteModal(e, note.note_id, note.note)
                        }
                      />
                      <i
                        id="delete"
                        className="material-icons"
                        onClick={(e) =>
                          this.openConfirmDeleteModal(e, note.note_id)
                        }
                      >
                        delete
                      </i>
                    </span>
                  )}
                </p>
                <p className="accent-text">
                  {note.updated_by}
                  <i>
                    {" "}
                    at {moment(note.updated_at).format("LT")},{" "}
                    {moment(note.updated_at).format("L")}
                  </i>
                </p>
              </div>
            ))
          : ""}
        <div className="quote-add-note">
          <p className="orange-text" onClick={(e) => this.openNoteModal(e)}>
            <i className="material-icons icon-left">add</i>Add Note
          </p>
        </div>
        {this.props.notes && this.props.notes.length > 5 ? (
          <div className="quote-add-note">
            <p className="orange-text" onClick={this.showAllNotes}>
              {!this.state.showAllNotes
                ? "Show More Notes"
                : "Hide Additional Notes"}
            </p>
          </div>
        ) : (
          ""
        )}
        <ConfirmationModal
          isOpen={this.state.showDeleteModal}
          message="Are you sure you would like to delete this note?"
          close={() => this.setState({ showDeleteModal: false })}
          confirm={(e) => this.deleteNote(e)}
        />

        {this.state.noteModalOpen && (
          <NoteModal
            isOpen={true}
            onRequestClose={this.closeNoteModal}
            text={{
              title: this.state.note_id ? "Edit Note" : "Add Note",
              button: "Save",
            }}
            note={this.state.new_note}
            onSubmit={this.submitNote.bind(this)}
          />
        )}
      </div>
    );
  }
}

export default QuoteFormNotes;
